@use "../abstracts/mixins" as *;
@use "../base/colors" as *;

.header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height:100dvh;
	background-image: url("../../dist/img/header_small.png");
	background-size: cover;
	background-position: center;
	z-index: 1;
	overflow: hidden;
	&::after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(20, 23, 25, 0.98);
		z-index: -1;
	}
	&__box {
		&-heading {
			line-height: 1.3;
			color: #edf2ff;
			font-size: clamp(2.5rem, 2.5rem + 3.2037vw, 8rem);

			& span {
				display: inline-block;
				position: relative;
				// background-color: #ffb800;
				// transform: rotate(-3deg);
				width: max-content;
				padding: 0.3em 0.2em;
				z-index: 1;
				&::after {
					position: absolute;
					content: "";
					top: 50%;
					left: 0;
					width: 100%;
					height: 60%;
					transform: translate(0, -50%);
					border-radius: 17px;
					background-color: #fdb802;
					z-index: -1;
				}
			}
		}
		&-text {
			font-size: clamp(1.6rem, 1.2308rem + 1.0256vw, 2rem);
			color: #dcdcdc;
			font-weight: 200;
		}
	}
	&__buttons {
		display: flex;
		gap: 3em;
		justify-content: center;
		margin-top: 5em;
		flex-direction: column;
		&-button {
			text-align: center;
			padding: 0.5em 3em;
			color: #fff;
			font-size: clamp(1.8rem, 0.8769rem + 2.5641vw, 2.4rem);
			border-radius: 15px;
			font-weight: 600;
			&--first {
				background-color: #fdb802;
			}
			&--second {
				border: 2px solid #fff;
			}
		}
	}
	&__arrow {
		& img{
			height: 40px;
		}
		position: absolute;
		bottom: 10em;
		z-index: 1;
		animation: upAndDown 1s infinite;
	}
}

@keyframes upAndDown {
	0% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(20px);
	}
	100% {
		transform: translateY(0);
	}
}
