* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
  padding: 0em 3em;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style-type: none;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100dvh;
  background-image: url("../../dist/img/header_small.png");
  background-size: cover;
  background-position: center;
  z-index: 1;
  overflow: hidden;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 23, 25, 0.98);
  z-index: -1;
}
.header__box-heading {
  line-height: 1.3;
  color: #edf2ff;
  font-size: clamp(2.5rem, 2.5rem + 3.2037vw, 8rem);
}
.header__box-heading span {
  display: inline-block;
  position: relative;
  width: max-content;
  padding: 0.3em 0.2em;
  z-index: 1;
}
.header__box-heading span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  width: 100%;
  height: 60%;
  transform: translate(0, -50%);
  border-radius: 17px;
  background-color: #fdb802;
  z-index: -1;
}
.header__box-text {
  font-size: clamp(1.6rem, 1.2308rem + 1.0256vw, 2rem);
  color: #dcdcdc;
  font-weight: 200;
}
.header__buttons {
  display: flex;
  gap: 3em;
  justify-content: center;
  margin-top: 5em;
  flex-direction: column;
}
.header__buttons-button {
  text-align: center;
  padding: 0.5em 3em;
  color: #fff;
  font-size: clamp(1.8rem, 0.8769rem + 2.5641vw, 2.4rem);
  border-radius: 15px;
  font-weight: 600;
}
.header__buttons-button--first {
  background-color: #fdb802;
}
.header__buttons-button--second {
  border: 2px solid #fff;
}
.header__arrow {
  position: absolute;
  bottom: 10em;
  z-index: 1;
  animation: upAndDown 1s infinite;
}
.header__arrow img {
  height: 40px;
}

@keyframes upAndDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}
.our-services .container {
  padding: 0 2em;
}

@media (max-width: 768px) {
  .our-services {
    overflow: hidden;
  }
}
.service__box {
  display: flex !important;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 4em 4em;
  text-align: center;
}
.service__box-image {
  background: #ffb800;
  padding: 3em;
  border-radius: 50%;
  margin-bottom: 2em;
}
.service__box-image img {
  height: 50px;
  width: auto;
}
.service__box-heading {
  font-size: clamp(2rem, 1.2rem + 2.2222vw, 2.8rem);
  font-weight: 700;
}
.service__box-text {
  font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 1.8rem);
  font-weight: 200;
  color: #1d1d1d;
}
.service__box-button {
  font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.4rem);
  padding: 0.6em 1.2em;
  margin-top: 4em;
  font-weight: 600;
  color: #fff;
  background-color: #1a2024;
  border-radius: 13px;
}

.section-padding {
  padding: 6vh 0;
}

.section__box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #131618;
}
.section__box-heading {
  font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.2rem);
}
.section__box-text {
  font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
}

.swiper {
  position: relative;
  width: 100%;
  margin-top: 10em;
  overflow: visible !important;
}
.swiper-pagination {
  position: relative !important;
  display: flex;
  margin-top: 5em;
  justify-content: center;
  align-items: center;
}
.swiper-container {
  position: relative;
}
.swiper-wrapper {
  display: flex;
  overflow: visible !important;
}
.swiper-button-prev, .swiper-button-next {
  border-radius: 50%;
  position: absolute;
  padding: 6em;
}
.swiper-button-prev {
  background-color: #ffb800;
  color: #fff !important;
  left: -8% !important;
  top: 25% !important;
  transform: translateY(8%, -25%);
}
.swiper-button-next {
  background-color: #fff;
  color: #ffb800 !important;
  top: 45% !important;
  left: -8% !important;
  transform: translateY(8%, -45%);
  padding: 6em;
  box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.25);
}
.swiper-slide {
  border-radius: 50px;
  height: 100%;
  width: auto;
  margin-right: 20px;
  transition: opacity 0.5s ease;
  box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.15);
}

.swiper-scrollbar {
  display: none;
}

@media (max-width: 768px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: none !important; /* Ukryj przyciski */
  }
  .swiper-pagination {
    display: block;
  }
}
@media (min-width: 576px) {
  .section__box {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .header__box {
    text-align: center;
  }
  .header__box-heading span {
    transform: rotate(-3deg);
  }
  .header__buttons {
    flex-direction: row;
  }
}
@media (min-width: 992px) {
  .header {
    background-image: url("../../dist/img/header_big.png");
  }
}