@media (max-width:768px) {
    .our-services{
        overflow: hidden;
    }
}
.service {
	&__box {
		display: flex !important;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		padding: 4em 4em;
		text-align: center;
		&-image {
			background: #ffb800;
			padding: 3em;
			border-radius: 50%;
			margin-bottom: 2em;
			& img {
				height: 50px;
				width: auto;
			}
		}
		&-heading {
			font-size: clamp(2rem, 1.2rem + 2.2222vw, 2.8rem);
			font-weight: 700;
		}
		&-text {
			font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 1.8rem);
			font-weight: 200;
			color: #1d1d1d;
		}
		&-button {
			font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.4rem);
			padding: 0.6em 1.2em;
			margin-top: 4em;
			font-weight: 600;
			color: #fff;
			background-color: #1a2024;
			border-radius: 13px;
		}
	}
}
