@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/about" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;

.section-padding {
	padding: 6vh 0;
}
.section__box {
	display: flex;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	color: #131618;
	&-heading {
		font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.2rem);
	}
	&-text {
		font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
	}
}
.swiper {
	position: relative;
	width: 100%;
	margin-top: 10em;
	overflow: visible !important;
    &-pagination{
        // display: none;
        position: relative !important;
        display: flex;
        margin-top: 5em;
        justify-content: center;
        align-items: center;
    }
	&-container {
		position: relative;
	}
	&-wrapper {
		display: flex;
		overflow: visible !important;
	}
	&-button-prev,
	&-button-next {
		border-radius: 50%;
		position: absolute;
		padding: 6em;
	}
	&-button-prev {
		background-color: #ffb800;
		color: #fff !important;
		left: -8% !important;
		top: 25% !important;
		transform: translateY(8%, -25%);
	}
	&-button-next {
		background-color: #fff;
		color: #ffb800 !important;
		top: 45% !important;
		left: -8% !important;
		transform: translateY(8%, -45%);

		padding: 6em;
		box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.25);
	}
	&-slide {
		border-radius: 50px;
		height: 100%;
		width: auto;
		margin-right: 20px;
		transition: opacity 0.5s ease;
		box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.15);
	}
}

.swiper-scrollbar {
	display: none;
}
@media (max-width: 768px) {
	.swiper-button-prev,
	.swiper-button-next {
		display: none !important; /* Ukryj przyciski */
	}
    .swiper-pagination {
        display: block;
    }
}
@media (min-width: 576px) {
	.section__box {
		text-align: left;
	}
}

@media (min-width: 768px) {
	.header {
		&__box {
			text-align: center;
			&-heading {
				& span {
					transform: rotate(-3deg);
				}
			}
		}
		&__buttons {
			flex-direction: row;
		}
	}
}

@media (min-width: 992px) {
	.header {
		background-image: url("../../dist/img/header_big.png");
	}
}

